.mkd-header-vertical-compact {
	.mkd-vertical-area-widget-holder {
		z-index: 100;
		bottom: 0px;
		left: 0px;
		color: #878787;

		text-align: center;

		> div {
			@include mkdTableLayout();
			height: 75px;
			border-bottom: 1px solid rgba(0, 0, 0, 0.1);

			> div, .mkd-search-opener {
				@include mkdTableCellLayout();
			}
		}
	}
}